import React, { useMemo } from 'react';
import { makeStyles, Theme, Typography, Box } from '@material-ui/core';
import { MENTORING_EXPIRY } from 'src/constants';
import FadeUp from 'components/Animated/FadeUp';
import SvgElements from './SvgElements';
import differenceInCalendarDays from 'date-fns/differenceInCalendarDays';

const useStyles = makeStyles<Theme>(({ typography, breakpoints, spacing, palette }) => ({
  root: {
    height: `100vh`,
    backgroundColor: '#FFFFFF',
    padding: `0 ${spacing(2)}px`,
  },
  contentsArea: {
    zIndex: 1,
  },
  svgArea: {
    zIndex: 0,
  },
  subtitleWrapper: {
    backgroundColor: '#C5E5DD',
    padding: '0 14px',
  },
  subtitle: {
    whiteSpace: 'nowrap',
    fontWeight: typography.fontWeightBold,
    fontSize: 24,
    lineHeight: 1,
    transform: 'translateY(-8px)',
    [breakpoints.down('md')]: {
      fontSize: 16,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    fontSize: 56,
    marginTop: 22,
    lineHeight: '81px',
    '& strong': {
      color: palette.primary.main,
    },
    [breakpoints.down('md')]: {
      fontSize: 24,
      marginTop: 0,
    },
  },
  description: {
    textAlign: 'center',
    marginTop: 32,
    fontSize: 18,
    [breakpoints.down('md')]: {
      fontSize: 14,
      marginTop: 16,
    },
  },
  cta: {
    marginTop: 78,
    fontSize: 24,
    fontWeight: typography.fontWeightBold,
    padding: '8px 50px',
    [breakpoints.down('md')]: {
      fontSize: 16,
      marginTop: 30,
    },
  },
}));

const today = new Date();
const expiryDiff = differenceInCalendarDays(MENTORING_EXPIRY, today);

interface Props {
  onClickApply?: () => void;
}
const IntroSection = ({}: Props) => {
  const styles = useStyles();
  const dday = useMemo(() => {
    if (expiryDiff === 0) {
      return 'D-Day';
    } else if (expiryDiff < -100) {
      return '';
    } else if (expiryDiff < 0) {
      return ' ';
    } else {
      return `D-${expiryDiff}`;
    }
  }, []);

  return (
    <Box className={styles.root} display="flex" justifyContent="center" alignItems="center">
      <Box display="flex" flexDirection="column" alignItems="center" className={styles.contentsArea}>
        {dday === '' ? null : (
          <div className={styles.subtitleWrapper}>
            <FadeUp delay={0}>
              <Typography variant="h1" className={styles.subtitle}>
                    {dday}
              </Typography>
            </FadeUp>
          </div>
        )}
        <FadeUp delay={0}>
          <Typography variant="h2" className={styles.title}>
            LUBYCON <strong>SANDBOX</strong>
          </Typography>
        </FadeUp>
        <FadeUp delay={0}>
          <Typography className={styles.description}>
                    ,       
            
            <br />
                    .
          </Typography>
        </FadeUp>
        <FadeUp delay={200}>
          <Typography className={styles.description}>
                       ,
            <br />
                         .
          </Typography>
        </FadeUp>
        <FadeUp delay={400}>
          <Typography className={styles.description}>
                     ...
            <br />
                        ?
          </Typography>
        </FadeUp>
      </Box>
      <div className={styles.svgArea}>
        <SvgElements />
      </div>
    </Box>
  );
};

export default IntroSection;
